.homepage {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("../src-aboutus/assets/gambar.jpg"); /* Ganti "gambar.jpg" dengan path ke gambar yang ingin Anda gunakan */
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.homepage-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  /* padding: 20px; */
  text-align: center;
  color: white;
  /* align-items: center;  */
  justify-content: center;
}

.homepage-container h1 {
  font-size: 48px;
  width: 100%;
  margin: 0;
}

.homepage-container button {
  margin-top: 20px;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #f44336;
  color: white;
  font-size: 16px;
  cursor: pointer;
}

.homepage-container button:hover {
  background-color: #d63b2f;
  font-weight: bold;
}

.homepage-container-p {
  width: 40%;
  font-size: 1.2rem;
  /* justify-content: center; */
  margin: 0 auto;
}
